
.fa {
    padding: 10px;
    font-size: 30px;
    width: 30px;
    text-align: center;
    text-decoration: none;
    border-radius: 50%;
  }

/* Add a hover effect if you want */
.fa:hover {
  opacity: 0.7;
}

/* Set a specific color for each brand */

/* Facebook */
.fa-facebook {
  background: #3B5998;
  color: white;
}

/* Twitter */
.fa-twitter {
  background: #55ACEE;
  color: white;}
.cont {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: start;
    height: 100vh; 
    border: 1px solid black;
    
  }
  
  .cont img {
    clip-path: circle(50% at 50% 50%);
    width: 8%;
    margin-top: 50px;
    border-radius: 50%;
    
    
  }
  
  .cont >p{
    margin: 10px 0;
    color: white;
    font-size: 25px;
  }
  .cont :nth-child(3){
    color: white;
    margin: 0;
    font-size: 18px;
  }
  
  .cont .button {
    display: flex;
    gap: 10px;
    margin-top: 20px;
    margin-bottom: 15px;
  }
  
  .cont .button a:not(.fa) {
    
    margin: 0 10px;
  }
  body{
    background-image: url("/home/musthu/Downloads/triangles-g80db29d32_1920.png");
    background-repeat: no-repeat;
    background-size: cover;
  }
  button{
    width: 30%;
    margin: 10px;
    padding: 15px;
    border-radius: 25px;
    border: none;
    font-size: 20px;
  }
  .load img{
    width: 1900px;
    height: 1000px;
    display: flex;
    justify-content: center;
    
    
  }